* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

button {
  padding: 10px;
  min-height: 45px;
  text-transform: uppercase;
}
/* COLOR SWITCHER */
.center {
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
}

/* TIMER */
.wrap {
  margin-left: auto;
  margin-right: auto;
}

#datetime-picker {
  display: inline-flex;
  width: calc(100% - 90px);
  height: 40px;
  margin-right: 22px;
}
.timer {
  margin-top: 30px;
  display: flex;
  gap: 30px;
}
.field {
  display: flex;
  flex-direction: column;
  font-weight: 600;
  text-align: center;
}
.value {
  font-size: 48px;
}
.label {
  text-transform: uppercase;
}